.feed{
    display: flex;
    flex: 0.6;
    flex-direction: column;
    margin: 0px 30px;
}
.feed__form{
    display: flex;
}
.feed .feed__input{
    display: flex;
    background-color: white;
    padding: 10px;
    border-radius: 10px;
    flex-direction: column;
}
.feed .feed__input form{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    flex: 1;

}
.feed .feed__input form input[type='text']{
    flex: 1;
    border-radius: 20px;
    padding: 0px 10px;
    margin-left: 10px;
    height: 40px;
    outline: 0;
    border: 1px solid #ccc;
}
.feed .feed__input form input[type='submit']{
    display: none;
}
.feed__options{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
}
.feed__options .options{
    display: flex;
    align-items: center;
    color: #333;
    font-size: 14px;
    border-radius: 5px;
    padding: 10px 10px;
}
.feed__options .options:hover{
    background-color: #efe7e7;
}
.feed__options .options svg{
    margin-right: 5px;
}